import uniFab from '@/components/uni-fab/uni-fab.vue';
import huiziDeatil from '@/components/huizi-deatil/huizi-detail.vue';
import navBar from "@/components/zhouWei-navBar";
import publicFnc from '@/static/js/public.js';
export default {
	data() {
		return {
			title: 'Hello',
			pattern: {
				color: '#3c3e49',
				selectedColor: '#007AFF',
				backgroundColor: '#fff',
				buttonColor: '#e80e09'
			},
			content: [{
					iconPath: '/static/logo.png',
					selectedIconPath: '/static/logo.png',
					text: '首页',
					model: 'home',
					active: true
				},
				{
					iconPath: '/static/logo.png',
					selectedIconPath: '/static/logo.png',
					text: '汇总',
					model: 'calendar',
					active: false
				}
			],
			huiziData: [],
			horizontal: 'right',
			vertical: 'bottom',
			direction: 'vertical',
			popMenu: true
		};
	},
	onLoad() {
		this.initToday();
	},
	methods: {
		trigger(e) {
			let model = e.item.model;
			switch (model) {
				case 'calendar':
					console.log('11');
					uni.navigateTo({
						url: '/pages/calendar/index',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
					break;
				default:
					console.log('butiao');
			}
		},
		initToday() {
			let date = new Date();
			let y = date.getFullYear();
			let m = date.getMonth();
			let d = date.getDate();
			let lunar = publicFnc.calendar.solar2lunar(y, m + 1, d);
			let today_data = {
				date: d,
				lunar: lunar.IDayCn,
				month: m,
				year:y
			}
			this.huiziData =  publicFnc.toDay.getToDayData( today_data );
		}
	},
	components: {
		uniFab,
		huiziDeatil,
		navBar
	}
};
